@include htags();



/* 
*
header style
*/
.site {
    overflow: hidden;
}

.site-header {
    display: flex;
    margin: 0 auto;
}

.site-branding {
    margin: auto 1.6rem;
    display: flex;
    float: left;

    a {
        text-decoration: none;
    }
}

.site-title {
    font-size: 2rem;
}

.site-title,
.site-description {
    white-space: nowrap;
    flex-direction: column;
    margin: 0.5rem;
}

.site-description {
    float: left;
    padding: 0.5rem;
    font-style: italic;
    vertical-align: bottom;

}

.menu-toggle {
    display: none;
}

.main-navigation {
    margin: auto 1rem;
    float: right;
    text-align: end;
    width: 100%;

    .menu,
    .sub-menu {
        padding: 0;
        margin: 0;

    }

    #primary-menu {
        display: inline-block;
        align-items: center;
        width: 100%;
    }

    ul ul {
        z-index: 9999;
        position: absolute;
        width: 10rem;
        padding: 0;
        margin: 0;

    }

    ul ul ul {
        right: 10rem;
        left: auto;
        top: 0;
        padding: 0;
        margin: 0;
    }

    ul ul ul ul {
        right: 10rem;
        left: auto;
        top: 0;
        padding: 0;
        margin: 0;
    }

    ul ul ul ul ul {
        right: 10rem;
        left: auto;
        top: 0;

    }

    li {
        text-align: center;
        font-size: $font_size*0.9;
        display: inline-block;
        position: relative;
        float: none;
        padding: 0.4rem;
        margin: 0 0.4rem;
    }

    li li {
        width: 10rem;
        margin: 0;
    }

    li a {
        display: block;
        margin: 0;
        padding: 0;
        width: 100%;

    }

    ul ul {
        visibility: hidden;
        opacity: 0;
    }

    ul li:hover>ul {
        visibility: visible;
        opacity: 1;
    }


}

ul.children {
    display: flex;
    flex-direction: column;
}

/*
*
Header responsiveness
*
*/
@media(max-width:$x-large) {
    @include htags(0.8rem);

    .site-branding {
        margin: auto 1.4rem;
    }

    .site-title {
        font-size: 1.8rem;
    }

    .site-title,
    .site-description {
        margin: 0.4rem;
    }

    .site-description {
        float: left;
        padding: 0.4rem;

    }


    .main-navigation {
        margin: auto 0.9rem;

        ul ul {
            width: 9rem;

        }

        ul ul ul {
            right: 9rem;

        }

        ul ul ul ul {
            right: 9rem;


        }

        li {
            font-size: $font_size*0.8;
            padding: 0.3rem;
            margin: 0 0.3rem;
        }

        li li {
            width: 9rem;
        }

    }





}

@media(max-width:$large) {
    @include htags(0.7rem);

    .site-branding {
        margin: auto 1.2rem;
    }

    .site-title {
        font-size: 1.6rem;
    }

    .site-title,
    .site-description {
        margin: 0.3rem;
    }

    .site-description {
        float: left;
        padding: 0.3rem;

    }


    .main-navigation {
        margin: auto 0.8rem;

        ul ul {
            width: 8rem;

        }

        ul ul ul {
            right: 8rem;

        }

        ul ul ul ul {
            right: 8rem;


        }

        li {
            font-size: $font_size*0.7;
            padding: 0.3rem;
            margin: 0 0.3rem;
        }

        li li {
            width: 8rem;
        }
    }




}

@media(max-width:$medium) {
    @include htags(0.6rem);

    .site-branding {
        margin: auto 1rem;
    }

    .site-title {
        font-size: 1.4rem;
        margin: 0.3rem;
    }


    .site-description {
        display: none;
    }

    .site-description {
        float: left;
        padding: 0.4rem;

    }


    .main-navigation {
        margin: auto 0.8rem;

        ul ul {
            width: 8rem;

        }

        ul ul ul {
            right: 8rem;

        }

        ul ul ul ul {
            right: 8rem;


        }

        li {
            font-size: $font_size*0.7;
            padding: 0.3rem;
            margin: 0 0.3rem;
        }

        li li {
            width: 8rem;
        }

    }



}

@media(max-width:$small) {


    .site-branding {
        margin: auto 0.8rem;
    }

    .site-title {
        font-size: 1.2rem;
        margin: 0.3rem;
    }


    .main-navigation {
        margin: auto 0.6rem;

        ul ul {
            width: 7rem;

        }

        ul ul ul {
            right: 7rem;

        }

        ul ul ul ul {
            right: 7rem;


        }

        li {
            font-size: $font_size*0.6;
            padding: 0.2rem;
            margin: 0 0.2rem;
        }

        li li {
            width: 7rem;
        }

    }



}

@media(max-width:$x-small) {


    .site-branding {
        margin: auto 0.8rem;
    }

    .site-title {
        font-size: 1rem;
        margin: 0.3rem;
    }

}

/* 
*
Site content style
*
*/

body,
.page {
    margin: 0;
}

.site-main {
    width: 70%;
    float: right;
    padding: 2rem;

}

.widget-area {
    width: 30%;
    float: left;
    padding: 2rem;
    padding-top: 3rem;
    overflow: hidden;
    word-break: normal;
    word-wrap: normal;
}

.no-sidebar {
    .site-main {
        width: 100%;
        float: none;
        padding: 2rem;
    }
}

.entry-header,
.entry-footer {
    text-align: center;
}

.entry-footer {
    span {
        display: inline-block;
        padding: 1rem;

    }
}

.entry-meta {
    margin-bottom: 1rem;

}

.post {
    word-break: break-word;
    padding: 1rem;
    border: 0.2rem solid #f2f2f2;

}

p,
pre,
ul,
ol,
code,
h1,
h2,
h3,
h4,
h5,
h6,
li,
em,
strong,
span {
    unicode-bidi: plaintext;
}

a {
    unicode-bidi: isolate;
}

.site-main {
    word-wrap: break-word;
}

.widget {
    padding: 1rem;
    border-bottom: 0.2rem solid #f2f2f2;

    ul {
        margin: 0;
        padding: 1rem;
        list-style: none;

        li {
            padding-top: 0.5rem;

        }
    }

}

.has-post-thumbnail {
    text-align: center;

    p {
        text-align: left;
    }

}

.entry-content {
    figure {
        text-align: center;
    }
}

blockquote {
    padding: 1rem;
    border: 0.2rem solid #f2f2f2;
}

code {
    background-color: #f2f2f2;
    unicode-bidi: plaintext;
}

.comment {
    padding: 0.5rem;
    line-height: normal;
    border: 0.2rem solid #f2f2f2;
    background-color: #f8f8ff;
    box-shadow: inset 0 0.2rem 0.5rem #f2f2f2;

    li>ol {
        margin: 0;
    }
}

.format-image {
    text-align: center;
}

.widget ul {
    margin: 0.5rem;

}

.header-image img {
    width: 100%;
    height: 100%;
}

.nav-previous {
    float: right;
    width: 50%;
}

.nav-next {
    float: left;
    text-align: left;
    width: 50%;
}

.sticky {
    background-color: #ededed;
    background-image: url('http://example.com/wp-content/uploads/featured.png');
    background-repeat: no-repeat;
    background-position: right top;
}


/*
footer style 
*/
.site-footer {
    text-align: center;
    margin-top: 1rem;
    clear: both;
    background-color: $color_primary;
}

.site-info {
    padding: 0.5rem;
}


/*
*
Site content responsiveness
*/
@media(max-width:$x-large) {

    .site-main {
        padding: 1rem;
    }

    .widget-area {

        padding: 1rem;
        padding-top: 1.5rem;

    }

    .no-sidebar {
