            padding: 1rem;
        }
    }


    .entry-footer {
        span {
            padding: 0.5rem;

        }
    }

    .entry-meta {
        margin-bottom: 0.5rem;

    }

    .post {
        padding: 0.5rem;

    }

    .widget {
        padding: 0.5rem;

    }

    blockquote {
        padding: 0.5rem;

    }

    .comment {
        padding: 0.5rem;

    }

    ul,
    ol {
        margin: 0 1rem 0.5rem 0;
    }

    li>ol,
    li>ul {
        margin: auto
    }

}

@media(max-width:$large) {

    .site-main {
        padding: 0.9rem;
    }

    .widget-area {

        padding: 0.9rem;
        padding-top: 1.3rem;

    }

