        .site-main {
            padding: 1rem;
        }
    }


    .entry-footer {
        span {
            padding: 0.4rem;

        }
    }

    .entry-meta {
        margin-bottom: 0.4rem;

    }

    .post {
        padding: 0.4rem;

    }

    .widget {
        padding: 0.4rem;

    }

