}

@media(max-width:$medium) {

    .site-main,
    widget-area {
        @include htags(1rem);
    }

    .site-main {
        width: 100%;
        float: none;
        padding: 0.9rem;
    }

    .widget-area {
        width: 100%;
        float: none;
        text-align: left;
        padding: 0.9rem;
