    }

    li>ol,
    li>ul {
        margin: auto;
    }

}

@media(max-width:$small) {

    .site-main,
    widget-area {
        @include htags(0.8rem);
    }

    .site-main {

        padding: 0.8rem;
    }

    .widget-area {
        padding: 0.8rem;
        padding-top: 1.2rem;

    }

    .site-footer {
        font-size: $font_size * 0.4;
    }


    .comment {
        font-size: $font_size * 0.4;

    }

    ul,
    ol {
        margin: 0 0.4rem 0.1rem 0;
    }

    li>ol,
    li>ul {
        margin: auto;
    }

}

@media(max-width:$x-small) {

    .site-main,
    widget-area {
        @include htags(0.6rem);
    }

    .site-main {

        padding: 0.5rem;
        font-size: $font_size * 0.5;
    }

    .widget-area {
        padding: 0.5rem;
        padding-top: 0.9rem;
        font-size: $font_size * 0.5;

    }

    .entry-footer,
    .entry-meta {
        font-size: $font_size * 0.4;

        a {
            font-size: $font_size * 0.4;
        }
    }

    .site-footer {
        font-size: $font_size * 0.4;
    }

    ol.comment-list {
        padding: 0;
        margin: 0;
    }

    .comment {
        font-size: $font_size * 0.4;

        p {
            margin: 0;
        }

        ul,
        ol {
            margin: 0;
            padding: 0;
        }

        li>ol,
        li>ul {
            margin: auto;
        }
    }

    .widget ul {
        margin: 0;
    }

}

/*
*
site colors
*
*/
.site-header,
site-footer {
    background-color: $color_primary;
}

.site-header {

    a,
    a:visited,
    a:hover {
        color: $color-header_links;
    }
}

.site-title,
site-description {
    color: $color_header_links;

    a {
        color: $color_header_links;
    }
}

.main-navigation {

    ul,
    li {
        background-color: $color_primary;
    }

    ul,
    a,
    li a {
        color: $color_header_links;
    }

    a:active,
    a:hover,
    a:focus {
        color: $color_links_hover;
    }

    li li:hover>a,
    li li:hover {
        color: $color_header_links;
        background-color: $color_links_hover;
    }

    li li {
        border-bottom: 1px solid #fff;
    }
}

.site-main,
widget-area {

    a,
    a:visited {
        color: $color_links;
    }

    a:hover {
        color: $color_links_hover;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: $color_headings;
}

p {
    color: $color_text;
}

.entry-meta span {
    background-color: #f4f6f8;
    color: #616571;

    a {
        color: #616571;
        unicode-bidi: normal;
    }

    a:hover {
        color: $color_links_hover;
    }
}

.entry-footer {
    border-bottom: 0.2rem solid $color_border;

    span {
        border-top: 0.2rem solid $color_border;
    }
}

.site-info {
    h4 {
        color: $color_header_links;
    }
}

/*
*
Site icons
/*
*/
.menu-item-has-children>a::after {
    margin: 0.2rem;
    position: absolute;
    font-family: $font_awesome;
    content: "\f0d7";
    vertical-align: middle;
}

.page_item_has_children>a::after {
    margin: 0.2rem;
    position: absolute;
    font-family: $font_awesome;
    content: "\f0d7";
    vertical-align: middle;
}

a.open::after {
    margin: 0.2rem;
    position: absolute;
    content: "\f0d8";
    vertical-align: middle;

}

.edit-link a::before {
    unicode-bidi: isolate;
    font-family: $font_awesome;
    content: "\f044";
    margin: 0.2rem;
}

.comments-link a::before {
    unicode-bidi: isolate;
    font-family: $font_awesome;
    content: "\f075";
    margin: 0.2rem;
}

.cat-links a::before {
    unicode-bidi: isolate;
    font-family: $font_awesome;
    content: "\f07c";
    margin: 0.2rem;
}

.tags-links a::before {
    unicode-bidi: isolate;
    font-family: $font_awesome;
    content: '\f02c';
    margin: 0.2rem;
}

.widget_recent_entries li::before {
    unicode-bidi: isolate;
    font-family: $font_awesome;
    content: "\f15b";
    margin: 0.2rem;
}

.widget_pages li::before {
    unicode-bidi: isolate;
    font-family: $font_awesome;
    content: "\f15b";
    margin: 0.2rem;
}

.widget_recent_comments li::before {
    unicode-bidi: isolate;
    font-family: $font_awesome;
    content: "\f075";
    margin: 0.2rem;
}

.widget_archive li::before {
    unicode-bidi: isolate;
    font-family: $font_awesome;
