}

.reply a::before {
    unicode-bidi: isolate;
    font-family: $font_awesome;
    content: "\f064";
    margin: 0.2rem;
}

/*
*
site bar menu
*
*/
.fa-navicon,
.fa-close {
    display: none !important;
}

@media(max-width:$medium) {

    .fa-navicon,
    .fa-close {
        position: absolute;
        display: inline-block !important;
        padding: 0.6rem;
        right: 0;
        left: 85%;
        margin: 0;
        color: $color_header_links;
        font-size: $font_size*0.9;

        &:hover,
        &:focus {
            cursor: pointer;
        }
    }

    .active {
        ul {
            ul a::before {
                content: ">>";
            }

            ul ul a::before {
                content: ">>>"
            }

            ul ul ul a::before {
                content: ">>>>"
            }

            ul ul ul ul a::before {
                content: ">>>>>"
            }

            ul ul ul ul ul a::before {
                content: ">>>>>>"
            }
        }
    }

    .menu-bar {
        display: none;
    }

    .menu-bar ul {
        display: block;
        position: fixed;
        background-color: $color_primary;
        margin: 0;
        padding: 0;
        width: 15rem;
        height: 100%;
        left: 0;
        overflow: auto;

        li {
            display: list-item;
            padding: 0;
            background-color: $color-primary;
            text-align: center;
            position: relative;
            font-size: $font_size*0.6;
            line-height: 2;

            &:hover,
            &:focus,
            &:hover>a,
            &:focus>a {
                pointer-events: initial;
                background-color: $color_links_hover;
            }
        }

        ul {
            display: none;

            li {
                display: none;
            }
        }

        ul {
            padding: 0;
            overflow: hidden;
        }

        ul.childopen {
            display: block;
            position: relative;

            li {
                display: list-item;
                position: relative;
                margin-left: 0.3rem;
                margin-right: 0rem;
                max-width: 20ch;

            }
        }
    }

    #primary-menu>li:first-child {
        margin-top: 1rem;
    }

    #primary-menu>li:last-child {
        margin-bottom: 4rem;
    }
}

@media(max-width:$small) {

    .fa-navicon,
    .fa-close {

        padding: 0.5rem;
        font-size: $font_size*0.8;
    }

    .menu-bar ul {
        width: 15rem;
    }
}

@media(max-width:$x-small) {

    .fa-navicon,
    .fa-close {
        font-size: $font_size*0.7;
    }

    .menu-bar ul {
        width: 12rem;
    }
}

/*
*
* Social icons
*/
.usr_txt {
    display: none;
}

nav.social ul {
    display: flex;
    position: relative;
    margin: 0;
    padding: 0.5rem 0 0;
    justify-content: space-around;

}

nav.social ul li {
    display: inline-block;
    position: relative;
    font-family: $font_awesome;
    font-size: $font_size;
    list-style: none;
    width: 100%;
    padding: 0.5rem;
    border: 1px solid;
    background-color: $color_primary;

    a {
        color: #fff;
    }

    a:hover {
        color: $color_links_hover;
    }

    a[href*="facebook.com"]::before {
        content: "\f09a";
    }

    a[href*="twitter.com"]::before {
        content: "\f099"
    }

    a[href*="instagram.com"]::before {
        content: "\f16d";
    }

    a[href*="linkedin.com"]::before {
        content: "\f0e1";
    }
}

/*
*
footer sidebar
*
*/
.footer-sidebar {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    overflow: hidden;

    .widget {
        display: block;
        padding: 0.5rem;
        margin: 0;
        word-wrap: break-word;
        word-break: break-all;

        ul {
            padding: 0;
            margin: 0;
            text-align: start;
            font-size: 0.6*$font_size;
        }

    }

    .widget {
        width: 25%;
    }

    .widget:only-child {
        width: 100%;
        text-align: center;
    }

    h2,
    a,
    .widget li a {
        color: #fff;
    }

    a:hover {
        color: $color_links_hover;
    }
}

@media(max-width:$small) {
    .footer-sidebar {
        .widget {
            width: 50%;
        }
    }
}

@media(max-width:$x-small) {
    .footer-sidebar {
        .widget {
            width: 100%;
        }
    }
}

/*
*
social-widget
*
*/
.social-icon {
    display: block;
}

.social-icons-wraper {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
